* {
  text-decoration: none;
  list-style: none;
  font-family: "Gotu", sans-serif;
  box-sizing: border-box;
}

.navbar {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  display: flex;
  padding: 2.5rem;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.navbar__logo {
  color: #000000;
  font-size: 2.125rem;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.navbar__logo:hover {
  transform: scale(1.05, 1.07);
  transition-timing-function: ease-out;
  transition-duration: 250ms;
}
.navbar__bars {
  opacity: 0;
}
@media screen and (max-width: 768px) {
  .navbar__bars {
    color: #000000;
    opacity: 1;
    position: absolute;
    right: 1.25rem;
    font-size: 2rem;
  }
}
@media screen and (max-width: 768px) {
  .navbar__menu {
    display: none;
  }
}
.navbar__menu--links {
  color: #000000;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-bottom: 2.5rem;
  font-size: 1.125rem;
  border-bottom: 4px solid transparent;
}
.navbar__menu--links:hover {
  color: #4c6881;
  border-color: #4c6881;
}
.navbar__menu #button {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.625rem;
  background-color: #4c6881;
  color: #d7d7d7;
}
.navbar__menu #button:hover {
  background-color: #3c6181;
  color: #ffffff;
  transition: all 0.4s ease;
}

.hero {
  background-color: #ffffff;
  height: 100vh;
  width: 100vw;
}
.hero__container {
  height: 100vh;
  display: flex;
}
@media screen and (max-width: 768px) {
  .hero__container {
    display: flex;
    flex-direction: column;
  }
}
.hero__container--left {
  width: 40vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-top: 6.25rem;
}
@media screen and (max-width: 768px) {
  .hero__container--left {
    width: 100vw;
    margin-top: 3.125rem;
    padding-bottom: 3.125rem;
    font-size: 1.125rem;
  }
}
.hero__container--right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 60vw;
}
@media screen and (max-width: 768px) {
  .hero__container--right {
    width: 100%;
  }
}
.hero__container--right img {
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 768px) {
  .hero__container--right img {
    width: 100vw;
    height: 100vh;
  }
}
.hero__container--left h1 {
  font-size: 2.125rem;
}
@media screen and (max-width: 768px) {
  .hero__container--left h1 {
    font-size: 2.125rem;
  }
}
.hero__container--left h2 {
  font-size: 1.75rem;
}
@media screen and (max-width: 768px) {
  .hero__container--left h2 {
    font-size: 1.75rem;
  }
}
@media screen and (max-width: 768px) {
  .hero__container--left p {
    font-size: 1.125rem;
  }
}
.hero__container--btn {
  background-color: #4c6881;
  border: none;
  padding: 1rem 1.75rem;
  border-radius: 0.5rem;
  margin-top: 1.875rem;
  cursor: pointer;
}
.hero__container--btn:hover {
  background-color: #3c6181;
  transition: all 0.4s ease;
  transform: scale(1.05, 1.07);
  transition-timing-function: ease-out;
  transition-duration: 250ms;
}
.hero__container--btn a {
  color: #ffffff;
}